.wrapper {
  position: relative;
  width: 100%; }
  .wrapper .decorate {
    position: absolute;
    top: 0;
    z-index: 100;
    width: 100%; }
    .wrapper .decorate .logo {
      position: relative;
      top: -0.14667rem;
      margin: 0 auto;
      padding: 0;
      text-align: center;
      font-size: 0.25333rem;
      font-weight: 400; }
    .wrapper .decorate .left, .wrapper .decorate .right {
      position: absolute;
      top: 0.29333rem;
      width: 0.32rem; }
    .wrapper .decorate .left {
      left: 0.18667rem; }
    .wrapper .decorate .right {
      right: 0.18667rem; }
    .wrapper .decorate .dotWrapper {
      position: relative;
      top: -0.29333rem;
      z-index: 100;
      margin: 0 auto;
      width: 3.76rem; }
      .wrapper .decorate .dotWrapper .dotsLeft, .wrapper .decorate .dotWrapper .dotsRight {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        display: flex; }
        .wrapper .decorate .dotWrapper .dotsLeft .dot, .wrapper .decorate .dotWrapper .dotsRight .dot {
          display: inline-block;
          width: 0.08rem;
          height: 0.08rem;
          background-color: #111; }
      .wrapper .decorate .dotWrapper .dotsLeft .dot {
        margin-right: 0.08rem; }
        .wrapper .decorate .dotWrapper .dotsLeft .dot:first-child {
          width: 0.32rem; }
      .wrapper .decorate .dotWrapper .dotsRight {
        justify-content: flex-end;
        /* stylelint-disable-next-line */ }
        .wrapper .decorate .dotWrapper .dotsRight .dot {
          margin-left: 0.08rem; }
          .wrapper .decorate .dotWrapper .dotsRight .dot:last-child {
            width: 0.32rem; }
  .wrapper .center {
    position: relative;
    z-index: 99;
    margin: 0 auto;
    padding: 0.32rem;
    width: 3.76rem;
    border: 0.08rem solid #111;
    border-top: none;
    border-radius: 0 0 0.06667rem 0.06667rem; }
    .wrapper .center .screenWrapper {
      width: 100%;
      border: 0.03333rem solid;
      border-color: #987f0f #fae36c #fae36c #987f0f;
      background-color: #9ead86; }
      .wrapper .center .screenWrapper .screen {
        padding: 0.06667rem;
        width: 100%;
        border: 0.01333rem solid #453e36;
        display: flex; }
        .wrapper .center .screenWrapper .screen .mainPanel {
          padding: 0.01333rem 0.02667rem;
          width: 1.73333rem;
          border: 0.02667rem solid #000000;
          font-size: 0; }
        .wrapper .center .screenWrapper .screen .statusPanel {
          width: 1.06667rem;
          background: red; }
